import React, { Component } from "react";
import { FormGroup, FormControl, ControlLabel, Button, PageHeader, Grid, Row, Col, Image } from "react-bootstrap";
import "./Play.css";

export default class Play extends Component {
  constructor(props) {
    super(props);

    this.state = {
      codeword: "",
      hint1: "",
      hint2: "",
      hint3: ""
    };
  }

  validateForm() {
    return this.state.codeword.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  clearHints() {
    this.setState({
      hint1: "",
      hint2: "",
      hint3: ""
    });
  }

  handleSubmit = event => {
    event.preventDefault();

    const rawResponse = fetch('http://api.suber.games/api/login', {
      method: 'POST',
      credentials: 'include', //include cookies
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({code: this.state.codeword})
    }).then((response) => {
      console.log(response.status)
    }).catch((error) => {
      console.log(error)
    });
  }

  getHint = event => {
    event.preventDefault();
    console.log('hint?');
    this.clearHints();
    const rawResponse = fetch('http://api.suber.games/api/login', {
      method: 'POST',
      credentials: 'include', //include cookies
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({team_id: this.state.username, pass_code: this.state.password})
    }).then((response) => {
      console.log(response.status)
    }).catch((error) => {
      console.log(error)
    });
  }

  render() {
    return (
      <div className="Login">
        <Grid>
          <PageHeader>
            SUBER 2018 <br /><small> Post 1 </small>
          </PageHeader>
          <Col>
            <Row>
              <Image src="https://radioh.no/wp-content/uploads/2017/08/sildabord.jpg" rounded responsive />
            </Row>
            <Row>
              <p>Denne karen spiller i et jazz-band. Dette bandet er oppkalt etter et sted, og det er der vi vil!</p>
            </Row>
            <Row>
              <br />
            </Row>
            <Row>
              <form onSubmit={this.handleSubmit}>
                <FormGroup controlId="codeword" bsSize="large">
                  <ControlLabel>Kodeord</ControlLabel>
                  <FormControl
                    autoFocus
                    type="text"
                    value={this.state.codeword}
                    onChange={this.handleChange}
                  />
                </FormGroup>
                <Button
                  block
                  bsSize="large"
                  disabled={!this.validateForm()}
                  type="submit"
                >
                  Send inn svar
                </Button>
              </form>
            </Row>
            <Col>
              <h3>Hint</h3> <br />
              <p>{this.state.hint1}</p>
              <p>{this.state.hint2}</p>
              <p>{this.state.hint3}</p>
              <Button
                block
                bsSize="large"
                type="click"
                onClick={this.getHint}
              >
                Vis neste hint
              </Button>
            </Col>
            <Row>
              <br />
            </Row>
          </Col>
        </Grid>
      </div>
    );
  }
}



